.header {
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4));
  background-blend-mode:lighten;
  /* background-image: url(../images/houseSpecialVermicilliesResize.JPG); */
  background-color: black;
  background-size:cover ;
  background-repeat: no-repeat;
  background-position: top;
  background-attachment: fixed;
  margin-top: auto;
  opacity: 1;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  
}

.hero .title {
  color: red;
  font-size: 70px;
  transform: translate(0, -70px);
  
}

.hero .mini-title {
  color: red;
  transform: translate(0, -95px);
  font-size: 25px;
}

.hero-button {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  padding: 13px 20px;
  transform: translate(0, -80px);
  border-radius: 200px;
  font-weight: bold;
  color: rgb(61, 9, 9);
  background: rgba(241, 168, 72, 0.678);
  cursor: pointer;
  border: 2px rgba(241, 168, 72, 0.678);
  margin-top: 10px;
}

.hero-button:hover {
  background: rgb(61, 9, 9);
  color: rgba(241, 168, 72, 0.678);
  border: none;
}

.pulstate {
  animation: pulstate 3s ease-in-out;
  animation-iteration-count: infinite;
  opacity: 1;
  color: #000;
}


@media screen and (min-height: 750px) {
  .hero .title {
    transform: translate(0, -175px);
  }

  .hero .mini-title {
    transform: translate(0, -200px);
  }
 
}
@media screen and (min-height: 950px) {
  .hero .title {
    font-size: 100px;
    transform: translate(0, -200px);
  }
  .hero .mini-title {
    font-size: 45px;
    transform: translate(0, -240px);
  }
}

.ml3 {
  font-weight: 900;
  font-size: 3.5em;
  margin-top: 150px;
}

/********************** CSS ANIMATION EFFECT 5 **********************/
.ml5 {
  position: relative;
  font-weight: 300;
  font-size: 4.5em;
  color: red;
  padding-top: 1%;
}

.ml5 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.1em;
  padding-right: 0.05em;
  padding-bottom: 0.15em;
  line-height: 1em;
}

.ml5 .line {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 3px;
  width: 100%;
  background-color: red;
  transform-origin: 0.5 0;
}

.ml5 .ampersand {
  font-family: Baskerville, serif;
  font-style: italic;
  font-weight: 400;
  width: 1em;
  margin-right: -0.4em;
  margin-left: -0.3em;
}

.ml5 .letters {
  display: inline-block;
  opacity: 0;
}

.noodle-icon {
  position:relative;
  padding-top:1%;
  margin-top: 1px;
}