.menu-section {
    padding-bottom: 83px;
    background-image: linear-gradient( rgba(0, 0, 0.5, 0.9), rgba(0, 0, 0, 0.9)), url("https://images.pexels.com/photos/6646031/pexels-photo-6646031.jpeg?cs=srgb&dl=pexels-rodnae-productions-6646031.jpg&fm=jpg");
    width: 100%;
    height: 100%;
}

.squad {
    padding-top: 110px;
    padding-bottom: 115px;
}

.set-bg {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top right;
    background-attachment: scroll;
}

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.section-title.text-white svg {
    color: white;
}

.section-title svg {
    display: block;
    font-size: 55px;
}

.section-title h2 {
    color: orange;
}

svg {
    margin: auto;
}

@media only screen and (max-width: 767px) {
    .section-title h2 {
        font-size: 38px;
    }
}

.section-title {
    text-align: center;
    margin-bottom: 75px;
}

.text-white {
    color: white;
}

.flaticon-022-tray:before {
    content: "\f115";
}

.section-title h2::after, .section-title h2::before {
    position: absolute;
    content: "";
    width: 150px;
    height: 1px;
    background: orange;
    top: 32px;
}

.menu-tab-nav.nav-tabs {
    border-bottom: none;
    text-align: center;
    display: block;
    margin-bottom: 90px;
}

.menu-tab-nav.nav-tabs .nav-item {
    display: inline-block;
}

.nav-tabs .nav-item {
    margin-bottom: -1px;
}

.nav {
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none
}

.nav-link {
    display: block;
    padding: .5rem 1rem;
}

a {
    text-decoration: none;
}

.menu-tab-nav.nav-tabs .nav-item .nav-link {
    color: #fff;
    border: none;
}

.menu-tab-nav.nav-tabs .nav-item .nav-link.active {
    background-color: transparent;
    border: none;
    color: orange;
}