/************ MENU ITEM ***************/

.tab-content>.tab-pane {
    display: none;
}

.tab-content>.active {
    display: block;
}

.fade {
    transition: opacity .15s linear;
}

.fade:not(.show) {
    opacity: 0;
}

.menu-tab-nav.nav-tabs .nav-item .nav-link.active {
    background-color: transparent;
    border: none;
    color: #ffc515;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

@media (min-width: 992px) {
    .col-lg-6 {
        -ms-flex: 0 0 50%;
        flex: 0 0 50%;
        max-width: 50%;
    }
    .menu-page{
        max-width: 80%;
        margin-left: 40%;
    }
}


.col-lg-6 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

.menu-item {
    margin-bottom: 45px;
}

.menu-item h5 {
    color: orange;
    margin-bottom: 8px;
}

.menu-item .mi-meta {
    display: table;
    width: 100%;
    margin-top: -18px;
}

.menu-item p {
    font-size: 11px;
    line-height: normal;
    display: table-cell;
    width: 240px;
    padding-right: 1px;
    font-style: italic;
}

p {
    color: black;
    margin-top: 0;
    margin-bottom: 1rem;
}

.menu-item .menu-price {
    display: table-cell;
    color: white;
    text-align: right;
    position: relative;
    font-size: 17px;
}

.text-center {
    text-align: center!important;
    color: white;
    font-size: 15px;
}

.pt-5, .py-5 {
    padding-top: 3rem!important;
}

.note {
    margin-top: -60px;
}