.about-image {
  min-height: 80vh;
  background-image: url(../images/eggRollsResize.JPG);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left;
  background-attachment: fixed;
  
}

.welcome-message p {
  color: black;
}
.about-text {
  min-height: 80vh;
  align-items: center;
  background: rgba(95, 61, 16, 0.678);
  columns: #000;
  padding: 40px 0;
}

.about-text-center {
  width: 90%;
  margin: 25% auto;
}

.about-text h1 {
  font-size: 40px;
  margin-bottom: 10px;
  color: rgb(61, 9, 9);
}

.about-text p {
  margin: 30px 0;
  line-height: 2;
  color: black;
}


.about-text a {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  padding: 13px 20px;
  border-radius: 200px;
  font-weight: bold;
  color: black;
  background: rgba(95, 61, 16, 0.678);
  cursor: pointer;
  border: 2px black;
}

.about-text a:hover {
  color: rgba(241, 168, 72, 0.678);
  background: rgb(71, 30, 10);
  border: none;
}

@media screen and (min-width: 776px) {
  .about {
    display: flex;
  }
  .about-text {
    flex: 0 0 30%;
  }
  .about-image {
    flex: 0 0 70%;
    clip-path: polygon (0 0, 100% 0, 50% 100%, 0 100%);
  }
}

@media screen and (min-height: 680px) {
  .about-text{
    flex-wrap: wrap;
  }

}


/********************** CSS ANIMATION EFFECT 8 *********************/
h1.ml8 {
  font-weight: 900;
  font-size: 4.5em;
  color:rgb(61, 9, 9);
  width: 100%;
  height: 1%;
}

.ml8 .letters-container {
  position: relative;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  height: 1em;
}

.ml8 .letters {
  position: relative;
  z-index: 2;
  display: inline-block;
  line-height: 0.7em;
  right: -0.12em;
  top: -0.2em;
}

.ml8 .bang {
  font-size: 1.5em;
  top: auto;
  left:7%;
}

.ml8 .circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
}

.ml8 .circle-white {
  width: 3em;
  height: 3em;
  border: 2px dashed white;
  border-radius: 2em;
}

.ml8 .circle-dark {
  width: 2.2em;
  height: 2.2em;
  background-color: rgba(95, 61, 16, 0.678);
  border-radius: 3em;
  z-index: 1;
}

.ml8 .circle-dark-dashed {
  border-radius: 2.4em;
  background-color: transparent;
  border: 2px dashed rgba(95, 61, 16, 0.678);
  width: 2.3em;
  height: 2.3em;
}