#food {
  padding: 20px;
}

.food-container {
  max-width: 90vw;
  margin: 0 auto;
}

.food-card {
  position: relative;
  background: linear-gradient(rgba(205, 96, 0, 0, 7), rgba(205, 96, 0, 0, 7));
  margin: 20px 0;
  color: black;
  overflow: hidden;
  padding: 3px;
  border-radius: 16px;
  border: 1px solid orange;
  box-shadow: 2px 3px 1px 1px #cd6100;
}

#food img {
  width: 100%;
  min-width: 100%;
  display: block;
  transition: transform 4s;
}

.img-text {
  position: absolute;
  top: 80%;
  left: 40%;
  transform: trnaslate(-50%, -50%);
  text-align: center;
  opacity: 0;
  transition: opacity 4s;
}

.img-footer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 10px;
  font-size: 20px;
  opacity: 0;
  color: black;
  transition: opacity 4s;
}

.img-text h1 {
  font-size: 15px;
  align-items: center;
  margin-bottom: 10px;
  color: #cd6100;
}

.food-card:hover img {
  opacity: 0.4;
  transform: scale(1.3);
}

.food-card:hover .img-text, .food-card:hover .img-footer {
  opacity: 1;
}

.food-btn {
  display: inline-block;
  background: black;
  text-decoration: none;
  text-transform: capitalize;
  padding: 12px 32px;
  color: white;
  font-weight: bold;
  border-radius: 180px;
  cursor: pointer;
  margin-top: 16px;
  transition: all 0.3s ease-in-out;
  font-size: 80px;
  outline: none;
}

.food-btn:hover {
  color: orange;
  background: white;
  border: black;
  outline: none;
}

.fas {
  font-size: 30px;
}

@media screen and (min-width: 576px) {
  .food-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 16px;
  }
}

@media screen and (min-width: 992px) {
  .food-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 16px;
  }
  .img-text h1 {
    font-size: 25px;
  }
}