#footer {
  padding: 40px;
}

.footer {
  margin: 30px 5px 30px 5px;
  display: flex;
  
}
#text{
  color: black;
}
.footer-subtitle {
  text-transform: capitalize;
  color: orange;
  /*color: #cd6100;*/
  font-size: 25px;
}

.footer-info {
  margin-top: 10px;
  line-height: 1.5;
}

.footer-link-text span:hover {
  transition: transform 2s ease, color 2s ease;
  transform: translateY(-10px);
  color: #cd6100;
  cursor: pointer;
}

.footer-link-text {
  font-weight: bold;
  color: orange;
  align-content: center;
}

@media screen and (min-width: 576px) {
  .footer-center {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .footer {
    flex: 0 0 50%;
  }
}

@media screen and (min-width: 972px) {
  .footer {
    flex: 0 0 30%;
  }
  .footer-center {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
